import React, { Component } from 'react';

import PageHead from "../../components/PageHead/Index";
import { Form, FormItem } from '../../components/Form/Index';
import Toast from '../../components/Toast/Index';
import IconSpan from '../../components/IconSpan/Index';
import CropModal from '../../components/CropModal/Index';
import Image from '../../components/Image/Index';
import Progress from '../../components/Progress/Index';

import productAction from '../../request/product';
import fileAction from '../../request/file';
import Controller from '../../request/controller';
import ControllerPan from '../../request/controllerPan';

import FilePicker from '../../utils/filepicker';
import ImageProcessor from '../../utils/imageProcessor';
import mime from 'mime';

import './edit.css';

export default class Upload extends Component {
    constructor(props) {
        super(props);
        this.state = {
            state: false,
            data:"",
            brandData: [],
            seriesData: [],
            productId:'',
            name:'',
            brandId: '',
            seriesId: '',
            sliceIntroduce: [],
            progress:0
        }
    }

    async componentDidMount() {
        const id = this.props.match.params.id;
        this.ajaxBrandData();
        if (id) {
            const res = await productAction.get(id);
            if (res.data != null) {
                this.setState({
                    ...res.data
                });
                const regex = /https:\/\/wx.rocabin.com\/api\/file\/[^\"\']*/g;
                this.setState({
                    sliceIntroduce: res.data.introduce.match(regex)
                });

                this.getFile(res.data.thumbId, "thumbInfo", "thumb");
                this.ajaxSeriesData(res.data.brandId);
            }
        }
    }

    brandRequest = Controller.create("/brand");
    seriesRequest = Controller.create("/series");
    relativeInfoRequest = ControllerPan.create("/relativeInfo");
    fileRequest = ControllerPan.create("/file");

    async ajaxBrandData() {
        const res = await this.brandRequest.getAll();
        this.setState({
            brandData: res.data
        });
    }

    async ajaxSeriesData(brandId) {
        if (brandId && brandId !== '') {
            const res = await this.seriesRequest.getAll({ brandId });
            this.setState({
                seriesData: res.data
            });
        }
        else {
            this.setState({
                seriesData: []
            })
        }
    }

    async getFile(fileId, propName, fileName) {
        const res = await fileAction.get(fileId);
        const object = {};
        object[propName] = {
            blob: new Blob([res.data]),
            type: res.headers["content-type"],
            name: fileName + "." + mime.getExtension(res.headers["content-type"])
        }
        this.setState({
            ...object
        });
    }

    onBrandChange = (e) => {
        console.log(e.currentTarget.value);
        this.setState({
            brandId: e.currentTarget.value,
            seriesId: ''
        });
        this.ajaxSeriesData(e.currentTarget.value);
    }

    onSeriesChange = (e) => {
        this.setState({
            seriesId: e.currentTarget.value
        });
    }

    onPropChange = (prop, value) => {
        const object = {};
        object[prop] = value;
        this.setState({
            ...object
        })
    }

    getRelativeInfo = (data) =>{
        let formData = new FormData();
        formData.append("fileName",data.fileName);
        this.relativeInfoRequest.post(formData).then(res=>{
            if(res.errcode < 400){
                console.log(res);
                if(res.data[0] !== null){
                    this.ajaxSeriesData(res.data[0].hoppoBrands.id);
                    this.setState({
                        productId:res.data[0].id,
                        name:res.data[0].name,
                        brandId:res.data[0].hoppoBrands.id,
                        seriesId:res.data[0].hoppoSeriess.id
                    });
                }
            }
        })
    }

    selectFile = async (e) => {
        const file = await FilePicker.open({ multiple: false, filetypes: ["*"] });
        console.log(file);
        let map = {fileName:file[0].name,file:file[0]};
        console.log(map);
        this.setState({
            data:map
        });
        console.log(this.state.data);
        this.getRelativeInfo(this.state.data);
    }

    onStateChange = (state) => {
        this.setState({
            state: state
        });
    }

    saveCropData = (blob) => {
        this.setState({
            thumbInfo: {
                ...this.state.thumbInfo,
                blob: blob
            },
            state: false,
        });
    }

    uploadFile = async (propInfo) => {
        const res = await fileAction.create(new File([this.state[propInfo].blob], this.state[propInfo].name, { type: this.state[propInfo].type }));
        return res.data.body.id;
    }

    showError(text) {
        Toast.showToast(<IconSpan iconName="error" iconColor="error" text={text} />, "white");
    }

    submit =  () => {
        let formData = new FormData();
        formData.append("file",this.state.data.file);
        formData.append("productId",this.state.productId);
        let select = document.getElementById("brandId");
        formData.append("brand",select.options[select.selectedIndex].text);
        formData.append("name",this.state.name);
        let config = {
            onUploadProgress:progressEvent=>{
                let num = Math.round((progressEvent.loaded/progressEvent.total)*100);
                this.setState({progress:num});
            }
        }
        this.fileRequest.post(formData,config).then(res=>{
            if(res.errcode<400){
                Toast.showToast(<IconSpan iconName="success" iconColor="success" text={res.errmsg} />, "white");
                this.goback();
            }
        });

    }

    goback = () => {
        this.props.history.goBack();
    }

    render() {
        return (
            <div>
                <PageHead goback history={this.props.history}></PageHead>
                <div className="form-content">
                    <div className="form-main">
                        <Progress percentageNum={this.state.progress} progressName=""/>
                        <Form type="horizontal">
                            <FormItem for="introduce" title=":">
                                <button className="btn default" onClick={this.selectFile}></button>
                                <span>{this.state.fileInfo ? this.state.fileInfo.name : " "}</span>
                            </FormItem>
                            <FormItem for="fileName" title=":">
                                <input id="fileName" type="text" className="input" placeholder="" value={this.state.data.fileName} onChange={e => this.onPropChange("fileName", e.currentTarget.value)} disabled/>
                            </FormItem>
                            <FormItem for="sharedUrl" title=":">
                                <input id="sharedUrl" type="text" className="input" placeholder="" value={this.state.sharedUrl} onChange={e => this.onPropChange("sharedUrl", e.currentTarget.value)} disabled/>
                            </FormItem>
                            <FormItem for="brandId" title=":">
                                <select id="brandId" className="select" value={this.state.brandId} onChange={this.onBrandChange}>
                                    <option value=''>====</option>
                                    {this.state.brandData.map((item, index) => (
                                        <option key={index} value={item.id}>{item.name}</option>
                                    ))}
                                </select>
                            </FormItem>
                            <FormItem for="seriesId" title=":">
                                <select id="seriesId" className="select" value={this.state.seriesId} onChange={this.onSeriesChange}>
                                    <option value=''>====</option>
                                    {this.state.seriesData.map((item, index) => (
                                        <option key={index} value={item.id}>{item.name}</option>
                                    ))}
                                </select>
                            </FormItem>
                            {/* <FormItem for="fileType" title=":">
                                <select id="fileType" className="select" value={this.state.filetype} onChange={this.onFileTypeChange} disabled>
                                    <option value=''>====</option>
                                    {this.state.seriesData.map((item, index) => (
                                        <option key={index} value={item.id}>{item.name}</option>
                                    ))}
                                </select>
                            </FormItem> */}
                        </Form>
                        <div className="page-main-footer">
                            <button className="btn blue" onClick={this.submit}></button>&nbsp;&nbsp;&nbsp;
                            <button className="btn default" onClick={this.goback}></button>
                        </div>
                    </div>
                    <div className="form-preview" style={{ width: "24rem" }}>
                        <div className="preview">
                            {this.state.sliceIntroduce.map((item, index) => (
                                <Image key={index} style={{ width: "100%" }} src={item} />
                            ))}
                        </div>
                    </div>
                </div>
            </div >
        );
    }
}