import React, { Component } from 'react';
import { Form, FormItem } from '../../components/Form/Index';

//import setting from '../../appsettings.json';

import ControllerPan from '../../request/controllerPan';

import './index.css';
//import { request } from 'https';

export default class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: '',
            errcode: '',
            btnValue: '',
            btnDisable: ''
        }
    }

    loginRequest = ControllerPan.create("/login");

    login = () => {
        this.setState({
            errcode:"",
            btnValue:"...",
            btnDisable:"disabled"
        });
        let formDate = new FormData();
        formDate.append("username",this.state.username);
        formDate.append("password",this.state.password);
        this.loginRequest.post(formDate).then((res)=>{
            if(res.errcode < 400){
                sessionStorage.setItem("authorize",true);
                this.props.history.push("/");
            }else{
                this.setState({
                    errcode:""+res.errmsg,
                    btnValue:"",
                    btnDisable:""
                });
            }
        });
    }

    onChange = (prop) => {
        return (e) => {
            const changeProps = {};
            changeProps[prop] = e.currentTarget.value
            this.setState(changeProps);
        }
    }

    render() {
        return (
            <div>
                <div className="login-bg" style={{ backgroundImage: "url('http://cn.bing.com/az/hprichbg/rb/ElephantParade_ZH-CN13084036095_1920x1080.jpg')" }}></div>
                <div className="login-modal">
                    <input className="tx-login-state" type="text" disabled value={this.state.errcode} ></input>
                    <Form type="horizontal">
                        <FormItem for="username" title=":">
                            <input type="text" className="input" id="username" name="username" placeholder="" value={this.state.username} onChange={this.onChange("username")} />
                        </FormItem>
                        <FormItem for="password" title=":">
                            <input type="password" className="input" id="password" name="password" placeholder="" value={this.state.password} onChange={this.onChange("password")} />
                        </FormItem>
                    </Form>
                    <button className="btn blue btn-login" onClick={this.login} disabled={this.state.btnDisable}>{this.state.btnValue}</button>
                </div>
            </div>
        );
    }
}