import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

import './index.css';

export default class Sidebar extends Component {
    render() {
        return (
            <div className="nav">
                <NavLink exact className="nav-item" to={`${this.props.path}`}></NavLink>
                <NavLink exact className="nav-item" to={`${this.props.path}/article`}></NavLink>
                <NavLink exact className="nav-item" to={`${this.props.path}/brand`}></NavLink>
                <NavLink exact className="nav-item" to={`${this.props.path}/series`}></NavLink>
                <NavLink exact className="nav-item" to={`${this.props.path}/product`}></NavLink>
                <NavLink exact className="nav-item" to={`${this.props.path}/file`}></NavLink>
            </div>
        );
    }
}