import fileManageRequest from './basePan';

export default class ControllerPan {
    /**
     * 
     * @param {string} resoursePath 
     */
    constructor(resoursePath) {
        this.resoursePath = resoursePath;
    }

    static create(path){
        return new ControllerPan(path);
    }

    getAll(params) {
        return fileManageRequest.get(this.resoursePath, { params });
    }

    // get(id) {
    //     return fileManageRequest.get(this.resoursePath + "/" + id);
    // }

    // create(model) {
    //     return fileManageRequest.post(this.resoursePath, model);
    // }

    
    post(formData,config){
        return fileManageRequest.post(this.resoursePath,formData,config);
    }
    postJson(json){
        return fileManageRequest.post(this.resoursePath,json,{
            headers:{'Content-Type':'application/json'}
        });
    }
    // post(formData){
    //     console.log("configPOST");
    //     return fileManageRequest.post(this.resoursePath,formData,{
    //         headers: { 'Content-Type': 'multipart/form-data' }
    //     });
    // }

    put(formData) {
        return fileManageRequest.put(this.resoursePath,formData);
    }

    // createOrUpdate(id, model) {
    //     return id ? this.update(id, model) : this.create(model);
    // }

    delete(fileNames) {
        return fileManageRequest.delete(this.resoursePath,{data:fileNames});
    }
}

