
import React, {Component} from 'react';
import PropTypes from 'prop-types';
 
export default class Progress extends Component {
  constructor(props) {
    super(props)
  };
  static contextTypes = {
    tip: PropTypes.string,
    percentageNum: PropTypes.number,
    // allNum: PropTypes.number,
    progressName: PropTypes.string
  };
 
  render() {
    let percentageNum = (this.props.percentageNum);
    //css
    let leftPercentage = (100-this.props.percentageNum)*(-1);
    //,
    // let leftPercentage = (1-this.props.percentageNum)*(-450);
    let div1 = {
      //,
      // width:"450px"
      //css
        width:"100%",
        height:"35px",
        background:"#dedede",
        position: "relative",
        // margin:"10px auto 0",
        margin:"8px 0px 8px 0",
        overflow: "hidden",
      };
    let div2 = {
      //,
      // width:"450px"
      //css
        width:"100%",
        height:"35px",
        background:"#1AAAA8",
        position: "absolute",
      //,
        // left:`${leftPercentage}px`,
      //css
        left:`${leftPercentage}%`,
      };
    let div3 = {
        position: "absolute",
        width:"auto",
        height:"35px",
        left:"15px",
        color:"#ffffff",
        lineHeight: "35px",
        fontSize: "16px",
      };
    let div4 ={
        position: "absolute",
        width:"auto",
        height:"35px",
        right:"15px",
        lineHeight: "35px",
        fontSize: "16px",
        color: "#7B7B7B",
      };
    return (
      <div style={div1}>
        <div style={div2}></div>
        <div style={div3}>{this.props.progressName}</div>
        <div style={div4}>
          {this.props.tip}
                |
          {percentageNum}%
        </div>
      </div>
    )
  }
}