import React from 'react';

import axios from 'axios';

import Toast from '../components/Toast/Index';
import IconSpan from '../components/IconSpan/Index';

axios.defaults.withCredentials = true;//cookies*

const fileManageRequest = axios.create({
    baseURL: "https://pan.rocabin.com/api",
    // baseURL: "http://192.168.1.106:7000",
});

fileManageRequest.interceptors.response.use(res =>{
    if(res.data.errcode < 400){
        return res.data; 
    }
    else {
        Toast.showToast(<IconSpan iconName="error" iconColor="error" text={res.data.errmsg} />,"white");
        return res.data;
    }
},error =>{
    Toast.showToast(<IconSpan iconName="error" iconColor="error" text="" />, "white");
    let res = {"errcode":500,"errmsg":""};
    return res;
})

export default fileManageRequest;

