    getRelativeInfo = (data) => {
        let formData = new FormData();
        for(let i=0;i<data.length;i++){
            formData.append("fileName",data[i].fileName);
        }
        this.relativeInfoRequest.post(formData).then(res=>{
            if(res.errcode < 400){
                for(let i=0;i<data.length;i++){
                    if(res.data[i] !== null){
                        data[i].id = res.data[i].id;
                        data[i].brand = res.data[i].hoppoBrands.name;
                        data[i].name = res.data[i].name;
                        data[i].code = res.data[i].code;
                    }
                }
                this.setState({
                    data:data
                });
                console.log(this.state.data);
            }
        })
    }

    selectFile = async (e) => {
        const files = await FilePicker.open({ multiple: true, filetypes: ["*"] });
        let map = [];
        for(let i=0;i<files.length;i++){
            map.push({fileName:files[i].name,file:files[i]});
        }
        this.setState({
            data:map
        });
        this.getRelativeInfo(this.state.data);
    }

    /**
     * 
     * @param {number} index
     */
    onSelectChange = (index) => {
        this.setState({
            data: this.state.data.map((item, itemIndex) => {
                if (itemIndex === index) {
                    item.selected = !item.selected;
                }
                else {
                    item.selected = false;
                }

                return item;
            })
        })
    }

    /**
     * 
     * @param {number} index
     */
    onPageChange = (index) => {
        this.setState({
            pagingIndex: index
        });
        this.ajaxProductData(this.state.keyword, this.state.brandId, this.state.seriesId, index, this.state.pagingNumber);
    }

    setTableData() {
        return this.state.data.map(item => {
            return {
                ...item,
            }
        });
    }


    showError(text) {
        Toast.showToast(<IconSpan iconName="error" iconColor="error" text={text} />, "white");
    }

    showSelectedError() {
        this.showError("");
    }

    showSuccess(text) {
        Toast.showToast(<IconSpan iconName="success" iconColor="success" text={text} />, "white");
        this.ajaxProductData(this.state.keyword, this.state.brandId, this.state.seriesId, this.state.pagingIndex, this.state.pagingNumber);
    }

    getSelected() {
        return this.state.data.filter(item => item.selected === true);
    }

