import React, { Component } from 'react';

import PageHead from '../../components/PageHead/Index.jsx';
import Toolbox from '../../components/Toolbox/Index.jsx';
import Table from '../../components/Table/Index.jsx';
import { Form, FormItem } from '../../components/Form/Index';
import Toast from '../../components/Toast/Index';
// import Icon from '../../components/Icon/Index';
import IconSpan from '../../components/IconSpan/Index';

import Controller from '../../request/controller';
import ControllerPan from '../../request/controllerPan';

import { getQueryString } from '../../utils/url';

export default class File extends Component {
    constructor(props) {
        super(props);
        this.state = {
            brandData: [],
            seriesData: [],
            data: [],
            total: 0,
            pagingIndex: 1,
            pagingNumber: 10,//(pageSize)
            columns: [{
                key: "fileName",
                name: ""
            },{
                key: "fileType",
                name: "",
                width: "4rem"
            },{
                key: "sharedUrl",
                name: ""
            },{
                key: "views",
                name: "",
                width: "4rem"
            },{
                key: "information",
                name: ""
            }],
            //search     product
            brandId: getQueryString("brandId"),
            seriesId: getQueryString("seriesId"),
            fileType: getQueryString("fileType"),
            keyword: '',
        }
    }

    brandRequest = Controller.create("/brand");
    seriesRequest = Controller.create("/series");
    fileInfoRequest = ControllerPan.create("/fileInfo");
    multiFileRequest = ControllerPan.create("/multiFile");
    downloadRequest = ControllerPan.create("/download");
    test = ControllerPan.create("/test");

    async ajaxBrandData() {
        const res = await this.brandRequest.getAll();
        this.setState({
            brandData: res.data
        });
    }

    async ajaxSeriesData(brandId) {
        if (brandId && brandId !== '') {
            const res = await this.seriesRequest.getAll({ brandId });
            this.setState({
                seriesData: res.data
            });
        }
        else {
            this.setState({
                seriesData: []
            })
        }
    }

    async ajaxFileInfoData(pageIndex,pageSize){
        const res = await this.fileInfoRequest.getAll({pageIndex,pageSize});
        this.setState({
            ...res
        });
    }
    // async ajaxFileInfoData(keyword, fileType, brandId, seriesId, pageNumber, pageSize) {
    //     fileType = fileType && fileType !== '' ? fileType : undefined;
    //     brandId = brandId && brandId !== '' ? brandId : undefined;
    //     seriesId = seriesId && seriesId !== '' ? seriesId : undefined;
    //     if (file || brandId || seriesId) {
    //         const res = await fileInfoRequest.getAll({ keyword, fileType, brandId, seriesId, pageSize, pageNumber });
    //         alert(res.errmsg);
    //         this.setState({
    //             total: res.data.total,
    //             data: res.data
    //         })
    //     }
    //     else {
    //         this.setState({
    //             total: 0,
    //             data: []
    //         })
    //     }
    // }

    async componentDidMount() {
        this.ajaxBrandData();
        this.ajaxSeriesData(this.state.brandId);
        // this.ajaxFileInfoData(this.state.keyword, this.state.fileType, this.state.brandId, this.state.seriesId, this.state.pagingIndex, this.state.pagingNumber);
        this.ajaxFileInfoData(this.state.pagingIndex, this.state.pagingNumber);
    }

    onKeywordChange = (e) => {
        this.setState({
            keyword: e.currentTarget.value
        });
        this.ajaxFileInfoData(e.currentTarget.value, this.state.fileType, this.state.brandId, this.state.seriesId, this.state.pagingIndex, this.state.pagingNumber);
    }

    onFileTypeChange = (e) => {
        this.setState({
            fileType: e.currentTarget.value,
            //seriesId: ''
        });
        this.ajaxSeriesData(e.currentTarget.value);
        this.ajaxFileInfoData(this.state.keyword, e.currentTarget.value, this.state.brandId, this.state.seriesId, this.state.pagingIndex, this.state.pagingNumber);
    }

    onBrandChange = (e) => {
        this.setState({
            brandId: e.currentTarget.value,
            seriesId: ''
        });
        this.ajaxSeriesData(e.currentTarget.value);
        this.ajaxProductData(this.state.keyword, this.state.fileType, e.currentTarget.value, '', this.state.pagingIndex, this.state.pagingNumber);
    }

    onSeriesChange = (e) => {
        this.setState({
            seriesId: e.currentTarget.value
        });
        this.ajaxProductData(this.state.keyword, this.state.fileType, this.state.brandId, e.currentTarget.value, this.state.pagingIndex, this.state.pagingNumber);
    }

    /**
     * 
     * @param {number} index
     */
    onSelectChange = (index) => {
        this.setState({
            data: this.state.data.map((item, itemIndex) => {
                if (itemIndex === index) {
                    item.selected = !item.selected;
                }
                // else {
                //     item.selected = false;
                // }

                return item;
            })
        })
    }

    /**
     * 
     * @param {number} index
     */
    onPageChange = (index) => {
        this.setState({
            pagingIndex: index
        });
        this.ajaxFileInfoData(index,this.state.pagingNumber);
    }

    setTableData() {
        return this.state.data.map(item => {
            return {
                ...item,
                information:item.name ? item.brand+"/"+item.name+"/"+item.code : ""
            }
        });
    }


    showError(text) {
        Toast.showToast(<IconSpan iconName="error" iconColor="error" text={text} />, "white");
    }

    showSelectedError() {
        this.showError("");
    }

    showSuccess(message) {
        Toast.showToast(<IconSpan iconName="success" iconColor="success" text={message} />, "white");
        this.ajaxFileInfoData(this.state.pagingIndex, this.state.pagingNumber);
    }

    getSelected() {
        return this.state.data.filter(item => item.selected === true);
    }

    onUpload = () => {
        this.props.history.push("/file/upload");
    }

    onMultiUpload = () => {
        this.props.history.push("/file/multiUpload")
    }

    onEdit = () => {
        const selected = this.getSelected();
        if (selected.length !== 1) {
            this.showSelectedError();
            return;
        }
        this.props.history.push("/file/edit/" + selected[0].fileName);
    }

    onDelete = () => {
        const selected = this.getSelected();
        if (selected.length === 0) {
            this.showSelectedError();
            return;
        }
        let jsonArr = [];
        for(let i=0;i<selected.length;i++){
            let json = {"id":selected[i].id,"fileName":selected[i].fileName};
            jsonArr.push(json);
        }
        this.multiFileRequest.delete(jsonArr).then(res=>{
            if(res.errcode<400){
                this.showSuccess("");
            }
        });
    }

    onDownload = () => {
        const selected = this.getSelected();
        let fileName = selected[0].fileName;
        if(fileName===null || fileName ==="" || fileName === undefined){
            this.showSelectedError();
        }else{
            // window.location.href="http://192.168.1.103:7000/download?fileName="+fileName;
            window.location.href="https://pan.rocabin.com/api/download?fileName="+fileName;
            // this.downloadRequest.getAll({fileName})
        }
    }

    onExport = () => {
        // window.location.href="http://192.168.1.106:7000/export";
        window.location.href="https://pan.rocabin.com/api/export";
    }

    render() {
        return (
            <div>
                <PageHead></PageHead>
                <Form type="inline">
                    <FormItem for="keyword" title=":">
                        <input id="keyword" className="input" placeholder="" value={this.state.keyword} onChange={this.onKeywordChange} />
                    </FormItem>
                    <FormItem for="brandId" title=":">
                        <select id="brandId" className="select" value={this.state.brandId} onChange={this.onBrandChange}>
                            <option value=''>====</option>
                            {this.state.brandData.map((item, index) => (
                                <option key={index} value={item.id}>{item.name}</option>
                            ))}
                        </select>
                    </FormItem>
                    <FormItem for="seriesId" title=":">
                        <select id="seriesId" className="select" value={this.state.seriesId} onChange={this.onSeriesChange}>
                            <option value=''>====</option>
                            {this.state.seriesData.map((item, index) => (
                                <option key={index} value={item.id}>{item.name}</option>
                            ))}
                        </select>
                    </FormItem>
                    <FormItem for="fileType" title=":">
                        <select id="fileType" className="select" value={this.state.seriesId} onChange={this.onFileTypeChange}>
                            <option value=''>====</option>
                            {this.state.seriesData.map((item, index) => (
                                <option key={index} value={item.id}>{item.name}</option>
                            ))}
                        </select>
                    </FormItem>
                </Form>
                <Toolbox>
                    <button className="btn default" onClick={this.onMultiUpload}></button>
                    <button className="btn default" onClick={this.onUpload}></button>
                    <button className="btn default" onClick={this.onEdit}></button>
                    <button className="btn default" onClick={this.onDelete}></button>
                    <button className="btn default" onClick={this.onDownload}></button>
                    <button className="btn default" onClick={this.onExport}></button>
                </Toolbox>
                <Table
                    paging
                    pagingIndex={this.state.pagingIndex}
                    pageNumber={this.state.pagingNumber}
                    onPageChange={this.onPageChange}
                    total={this.state.total}
                    columns={this.state.columns}
                    data={this.setTableData()}
                    selectable
                    selectType="row"
                    onSelectChange={this.onSelectChange} />
            </div>
        );
    }
}